export default {
	data() {
		return {
			navList:['全部','待付款','待发货','待收货','待评价'],
			currentIndex: 0
		};
		
	},
	methods: {
		// 点击切换
		handleNav(index) {
			this.currentIndex = index
		},
		// 点击商品去到订单详情页
		orderDetail() {
			uni.navigateTo({
				url: '/pages/components/order-detail/order-detail'
			})
		},
		// 店家跳转到申请退货页面
		returnGoods() {
			uni.navigateTo({
				url: '/pages/components/return-goods/return-goods'
			})
		}
	}
}
